.page {
  color: #3e3e3e;
  scroll-behavior: smooth;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  line-height: 27px;
}

.page__body {
  margin: 0;
}

.page__menu {
  transition: transform .5s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  z-index: 1;
  transform: translateX(0);
}

.page__section {
  margin-bottom: 64px;
}

@media (width >= 1024px) {
  .page__section {
    margin-bottom: 104px;
  }
}

.page__section-title {
  text-align: center;
  letter-spacing: -1.5px;
  color: #000;
  margin: 0 0 32px;
  font-size: 36px;
  font-weight: bold;
  line-height: 36px;
}

@media (width >= 1024px) {
  .page__section-title {
    margin-bottom: 56px;
    font-size: 48px;
    line-height: 48px;
  }
}

.page__section-title:after {
  content: "";
  background-image: url("crown.29ff9104.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 23px;
  height: 16px;
  margin: 4px auto 0;
  display: block;
}

.container {
  max-width: 1020px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (width >= 1024px) {
  .container {
    padding: 0 40px;
  }
}

.grid {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width >= 1024px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
  }
}

.grid--medium {
  display: block;
}

@media (width >= 800px) {
  .grid--medium {
    display: grid;
  }
}

.grid--desktop {
  display: block;
}

