.logo__image {
  height: 48px;
}

@media (width >= 1024px) {
  .logo__image {
    height: 80px;
  }
}

.icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  padding-bottom: 100%;
  display: block;
}

.icon--menu {
  background-image: url("menu.3308bab0.svg");
}

.icon--menu:hover {
  background-image: url("menu_hover.05548180.svg");
}

.icon--phone {
  background-image: url("phone.9e1af677.svg");
}

.icon--cross {
  background-image: url("cross.7f340308.svg");
}

.top-actions {
  grid-template-columns: 18px 1fr 18px;
  place-items: center;
  display: grid;
}

@media (width >= 1024px) {
  .top-actions {
    grid-template-columns: 24px 1fr 24px;
  }
}

.header {
  background-image: url("header_miami.189312c9.png");
  background-position: center;
  background-size: cover;
}

.header__content {
  box-sizing: border-box;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  padding: 24px 0 80px;
  display: flex;
}

.header__title {
  letter-spacing: -1px;
  color: #fff;
  font-size: 46px;
  font-weight: bold;
  line-height: 40px;
}

@media (width >= 1024px) {
  .header__title {
    letter-spacing: -1.5px;
    font-size: 80px;
    line-height: 80px;
  }
}

.header__title:before {
  content: "";
  background-color: #fff;
  width: 40px;
  height: 2px;
  margin-bottom: 24px;
  display: block;
}

.header__call {
  text-decoration: none;
}

@media (width >= 1024px) {
  .header__call:before {
    content: "+1 234 555-55-55";
    letter-spacing: 2px;
    text-transform: uppercase;
    opacity: 0;
    color: #fff;
    pointer-events: none;
    width: max-content;
    padding-right: 20px;
    font-size: 12px;
    line-height: 16px;
    display: block;
    position: relative;
    top: 50%;
    transform: translate(-100%, 100%);
  }

  .header__call:hover:before {
    opacity: 1;
    pointer-events: all;
    transition: all .5s;
  }
}

.menu {
  background-color: #0075ff;
}

.menu__content {
  box-sizing: border-box;
  height: 100vh;
  padding: 24px 0;
}

.menu__top {
  margin-bottom: 48px;
}

.menu__list {
  margin: 0 0 48px;
  padding: 0;
  list-style: none;
}

.menu__item {
  margin-bottom: 24px;
}

.menu__item:last-child {
  margin-bottom: 0;
}

.menu__link {
  color: #fff;
  letter-spacing: 2px;
  text-transform: uppercase;
  border-bottom: 1px solid #0000;
  padding-bottom: 8px;
  font-size: 22px;
  font-weight: bold;
  line-height: 22px;
  text-decoration: none;
  transition: border-color .3s;
  display: inline-block;
}

.menu__link:hover {
  border-color: #fff;
}

.menu__call {
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #fff;
  font-size: 12px;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.menu__call--phone {
  margin-bottom: 16px;
}

.menu__call--action {
  border-bottom: 1px solid #fff;
  width: max-content;
  padding-bottom: 8px;
}

.about__title {
  letter-spacing: -1.5px;
  color: #000;
  align-items: center;
  margin: 0 0 48px;
  font-size: 36px;
  font-weight: bold;
  line-height: 36px;
}

@media (width >= 1024px) {
  .about__title {
    margin-bottom: 0;
    font-size: 48px;
    line-height: 48px;
  }
}

.about__title-link {
  color: #0075ff;
  text-decoration: none;
}

.about__subtitle {
  color: #0075ff;
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: bold;
  line-height: 30px;
  text-decoration: none;
  display: block;
}

.about__paragraf {
  margin: 0 0 27px;
}

.about__paragraf:last-child {
  margin-bottom: 0;
}

.about__paragraf--important {
  font-weight: bold;
}

.gallery__place {
  margin-bottom: 32px;
}

@media (width >= 1024px) {
  .gallery__place {
    margin-bottom: 40px;
  }
}

.place__photo-container {
  box-sizing: border-box;
  width: 100%;
  max-height: 256px;
  transition: transform .5s;
  position: relative;
  overflow: hidden;
}

.place__photo-container:hover {
  transform: scale(1.05);
}

.place__photo-container:after {
  content: "";
  padding-bottom: 100%;
  display: block;
}

@media (width >= 1024px) {
  .place__photo-container {
    height: 256px;
    padding-bottom: 0;
  }
}

.place__photo {
  object-fit: cover;
  object-position: center;
  border-radius: 8px;
  width: 100%;
  height: 100%;
  position: absolute;
}

.place__photos {
  margin-bottom: 16px;
}

@media (width >= 1024px) {
  .place__photos {
    margin-bottom: 24px;
  }
}

.place__link {
  color: #0075ff;
  font-weight: bold;
  text-decoration: none;
}

.button {
  box-sizing: border-box;
  color: #0075ff;
  cursor: pointer;
  border: 2px solid #0075ff;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 56px;
  font-size: 16px;
  font-weight: bold;
  line-height: 14px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.button--primary {
  color: #fff;
  background-color: #005ecc;
  border-color: #005ecc;
}

.button:hover {
